export default {
	created() {
		var num = this.number-1
		var neirong = this.questionList[num]
		if(this.tag == "l"){
			neirong = this.questionList[this.qNumber]
		}
		this.content = neirong.content
		this.jiexi = neirong.jiexi
		this.rightAnswer = neirong.rightchoice
		var a = `A. ${neirong.A}`
		var b = `B. ${neirong.B}`
		var c = `C. ${neirong.C}`
		var d = `D. ${neirong.D}`
		this.choices.push(a)
		this.choices.push(b)
		this.choices.push(c)
		this.choices.push(d)
		
	},
	data() {
		return {
			content: '',
			choice: '',
			choices:[],
			isActive: 99,
			num: 0,
			jiexi: '',
			rightAnswer: ''
		}
	},
	methods: {
		activeChoice (e){
			this.isActive = e
			if(e == 0){
				this.choice = "A"
			}
			if(e == 1){
				this.choice = "B"
			}
			if(e == 2){
				this.choice = "C"
			}
			if(e == 3){
				this.choice = "D"
			}
		},
		changeQuestion(e){
			var data ={}
			if(this.tag == "l"){
				data = {
					ps: e
				}
			}else {
				if(this.nextText == "提交"){
					data = {
						ps: e,
						number: this.number-1,
						answer: this.choice,
						submit: true,
						tixing: this.questionList[this.number-1].tixing
					}
				}else{
					data = {
						ps: e,
						number: this.number-1,
						answer: this.choice,
						submit: false,
						tixing: this.questionList[this.number-1].tixing
					}
				}
			}	
			this.$emit('func', data);
		}
	},
	props:[
		"number",
		"questionList",
		"nextText",
		"jiexiTag",
		"qNumber",
		"tag"
	]
}
